import React from "react";
import Image from '../../Elements/Media/Image';
import Video from '../../Elements/Media/Video';
import ViewCount from "../../Elements/ViewCount";
const Media = ({ Post, ThemeStyle, CardStyle, index, inView }) => {
  const isVideo = [3,5].includes(Post.type) && ThemeStyle.autoPlay === 1 && inView ? true : false
  return <div className="tb_rtp_media_wrap">
        {isVideo && CardStyle.viewCountStatus == 1 && Post?.count.views > 0 ? <ViewCount count={Post?.count.views} /> : null}
    {isVideo ?
      <Video VideoClass="tb_rtp_video" Post={Post} size={1} isCover={true} muted={true} autoPlay={true} index={index} inView={inView} />
      :
      <Image ImageClass="tb_rtp_image" Post={Post} size={1} index={index} inView={inView} />
    }
  </div>
}
export default Media